import MenuDark from '../imgsrc/hamburger_dark2.png'
import MenuLight from '../imgsrc/hamburger_light2.png'
import CloseDark from '../imgsrc/close_dark2.png'
import CloseLight from '../imgsrc/close_light2.png'
 
