import React, { Component } from 'react';
import LogoLight from '../imgsrc/Logo_lightmode.png';
import LogoDark from '../imgsrc/Logo_darkmode.png';
import {Container} from 'react-bootstrap';
import {Link} from 'react-router-dom';
import '../App.css';

export default class Header extends Component {
  state = {
    theme: ''
    };

    componentDidMount() {
        if (this.props.checked === false) {
            this.setState({
                theme: 'light'
            })
        } else {
            this.setState({
                theme: 'dark'
            })
        }
    }

    componentDidUpdate(prevProps) {
      if (this.props.checked !== prevProps.checked) {
      this.setState({
          theme: localStorage.getItem("theme")
      })
    }
  }
  
  scrollToBottom(){
    console.log("Clicked!");
    window.scrollTo(0, document.body.scrollHeight);
  };
  render() {
    return (  
         <Container>
         <nav>
            {this.state.theme==='light' && <img class="logo" src={LogoLight}/>}
            {this.state.theme==='dark' && <img class="logo" src={LogoDark}/>}
            <div class='ul-holder'>
            <ul>
               <Link to={process.env.PUBLIC_URL + '/'}><li class="nav-links">Hello!</li></Link>
               <Link to={process.env.PUBLIC_URL + '/about'}><li class="nav-links">About me</li></Link>
               <Link to={process.env.PUBLIC_URL + '/work'}><li class="nav-links">Work</li></Link>
               {/* <a href="https://medium.com/@adityagianto" target="_blank"><li class="nav-links">Blog</li></a> */}
               <Link onClick={() => this.scrollToBottom()}><li class="nav-links">Hit me up!</li></Link>
            </ul>
            </div>
         </nav>
         </Container>
    );
  }
}