import React, { Component } from "react";
import MenuButton from './MenuButton';
import LogoLight from '../imgsrc/Logo_lightmode.png';
import LogoDark from '../imgsrc/Logo_darkmode.png';
import {Link} from 'react-router-dom';
 
class Sidebar extends Component {
    constructor(props, context) {
        super(props, context);
       
        this.state = {
          visible: false,
          theme:''
        };
        this.handleMouseDown = this.handleMouseDown.bind(this);
        this.toggleMenu = this.toggleMenu.bind(this);
      }
      
      componentDidMount() {
        if (this.props.checked === false) {
            this.setState({...this.state,
                theme: 'light'
            })
        } else {
            this.setState({...this.state,
                theme: 'dark'
            })
        }
    }

      componentDidUpdate(prevProps) {
        if (this.props.checked !== prevProps.checked) {
        this.setState({...this.state,
            theme: localStorage.getItem("theme")
        })
      }
      }

      scrollToBottom(){
        console.log("Clicked!");
        window.scrollTo(0, document.body.scrollHeight);
        this.toggleMenu();
      };

      handleMouseDown(e) {
        this.toggleMenu();
        console.log("clicked");
        e.stopPropagation();
      }
      
      toggleMenu() {
        this.setState({
            visible: !this.state.visible
        });
      }


    render() {
      var visibility = "hide";
      if (this.state.visible) {
        visibility = "show";
      }
    return (
      <div id='sidebar-group'>
        {this.state.theme==='light' && <img class="logosidebar" src={LogoLight}/>}
        {this.state.theme==='dark' && <img class="logosidebar" src={LogoDark}/>}
        <MenuButton handleMouseDown={this.handleMouseDown} visible={this.state.visible} theme={this.state.theme}/>
        <div id="flyoutMenu"
           className={visibility}>
            <div>
               <div id="sidebar-link"><Link onClick={this.handleMouseDown} to={process.env.PUBLIC_URL + '/'}><li class="nav-links">Hello!</li></Link>
               </div><div id="sidebar-link">
               <Link onClick={this.handleMouseDown} to={process.env.PUBLIC_URL + '/about'}><li class="nav-links">About me</li></Link>
               </div><div id="sidebar-link">
               <Link onClick={this.handleMouseDown} to={process.env.PUBLIC_URL + '/work'}><li class="nav-links">Work</li></Link>
               </div><div id="sidebar-link">
               <Link onClick={() => this.scrollToBottom()}><li class="nav-links">Hit me up!</li></Link>
               </div>
            </div>
      </div>
      </div>
    );
  }
}
 
export default Sidebar;